.portal {
 &__modal {
  display: flex;
  align-items: center;
  justify-content: center;

  &--box {
   background: #fff;
   box-shadow: 0 5px 20px 0 rgba(126, 137, 140, 0.2);
   border-radius: 5px;
   border: 1px solid #b8c8cc;
   overflow: hidden;
   z-index: 1001;
   position: relative;
   width: 350px;
   max-width: 100%;
  }

  &--close {
   position: absolute;
   top: 15px;
   right: 20px;
   padding: 0;
   width: 20px;
   height: 20px;
   cursor: pointer;
   outline: none;
   text-align: center;
   border-radius: 5px;
   border: none;
   text-indent: -9999px;
   background: transparent
    url(data:image/png;base64,)
    no-repeat center center;
   background-size: 100%;
   margin: 0;
  }

  &--header {
   padding: 15px 55px 15px 20px;
   background: #edf5f7;
   color: #454b4d;
  }
  
  &--body {
    padding: 20px;
    line-height: 1.4;
    font-size: 14px;
    color: #454B4D;
    background: #fff;
    position: relative;
    z-index: 2;
  }
 }
}
